<template lang="pug">
.stage-container
  .figure-1
  v-layout(row justify-center).name-choose

    v-flex(md4)

      h1.name-choose__title Введите имя
      v-text-field.input(
        v-model="name"
        solo
        hide-details
      )
      template(v-if="name")
        v-btn.ml-0(@click="done" color="primary" depressed) Продолжить
  .brushed

</template>


<script>
export default {
  props: {
    next: { type: Function, default: () => {} },
  },
  data() {
    return {
      name: null,
    };
  },
  methods: {
    done() {
      this.$store.dispatch('SET_USER', { name: this.name });
      this.next();
    },
  },
};
</script>


