<template lang="pug">
.stage-container
  .figure-2
  v-layout(row justify-center).choose-gender
    v-flex(md4)
      h1.choose-gender__title Выберите пол
      v-list
        v-list-tile(@click="chooseFemale")
          v-list-tile-action
            v-icon(color="primary") person
          v-list-tile-content
            v-list-tile-title Девочка
        v-list-tile(@click="chooseMale")
          v-list-tile-action
            v-icon(color="primary") person
          v-list-tile-content
            v-list-tile-title Мальчик
</template>


<script>
import { GENDER } from '@/constants';

const GENDER_CLASSES = {
  male: [
    {
      class: 'signs',
      score: 1,
    },
    {
      class: 'technique',
      score: 2,
    },
    {
      class: 'sport',
      score: 2,
    },
    {
      class: 'hammer',
      score: 1,
    },
  ],
  female: [
    {
      class: 'nature',
      score: 1,
    },
    {
      class: 'person',
      score: 1,
    },
    {
      class: 'shape',
      score: 3,
    },
  ],
};


export default {
  props: {
    next: { type: Function, default: () => {} },
  },
  methods: {
    chooseFemale() {
      this.$store.dispatch('SET_USER', { gender: GENDER.female });
      this.$store.dispatch('WRITE_SCORE', { classes: GENDER_CLASSES.female });
      this.next();
    },
    chooseMale() {
      this.$store.dispatch('SET_USER', { gender: GENDER.male });
      this.$store.dispatch('WRITE_SCORE', { classes: GENDER_CLASSES.male });
      this.next();
    },
  },
};
</script>


<style lang="scss">
.game-test {

  .choose-gender {
    z-index: 1;

    &__title {
      text-transform: uppercase;
      text-align: center;
      margin-bottom: 10px;
      font-family: Roboto, sans-serif;
      color: #1a194e;
    }

  }
}
</style>
