<template lang="pug">
.question-list
  v-list
    v-list-tile(
      v-for="(answer, idx) in answers" :key="idx"
      :class="{ active: idx === selectedAnswerKey }"
      @click="selectedAnswerKey = idx"
    )
      v-list-tile-content
        v-list-tile-title {{ answer.title }}
  transition(name="fade")
    .dialog__selected(v-if="selectedAnswer")
      p
        strong Вы выбрали:
        span  {{ selectedAnswer.title }}
      p: v-btn(@click="done" color="primary" depressed) Продолжить
</template>


<script>
export default {
  props: {
    answers: { type: Array, required: true },
  },
  data() {
    return {
      selectedAnswerKey: null,
    };
  },
  computed: {
    selectedAnswer() {
      return this.answers[this.selectedAnswerKey];
    },
  },
  methods: {
    done() {
      this.$store.dispatch('WRITE_SCORE', { classes: this.selectedAnswer.classes });
      this.$emit('next');
    },
  },
};
</script>
