          classes: [
            {
              class: 'reliability',
              score: -1,
            },
          ],
        },
        {
          title: 'В прошлый вторник я уничтожил сразу восьмерых с помощью одного лишь футбольного мяча!',
          classes: [
            {
              class: 'reliability',
              score: -1,
            },
          ],
        },
        {
          title: 'Нет, господин, они не водятся там, откуда я родом',
          classes: [
            {
              class: 'reliability',
              score: 1,
            },
          ],
        },
        {
          title: 'С настоящими я никогда не бился, но играл в игры, где есть драконы',
          classes: [
            {
              class: 'reliability',
              score: 1,
            },
          ],
        },
      ],
    };
  },
  computed: {
    ...mapGetters({
      user: 'getUser',
    }),
    genderName() {
      return this.user.gender === GENDER.male ? 'сэр' : 'леди';
    },
  },
};
</script>
