<template lang="pug">
.game-test
  v-progress-linear(:value="stagesPercent"  height="6").ma-0
  .fixed-background
  v-container.py-0
    full-page(ref="fullpage" :options="options")
      .section(v-for="(item, idx) in stagesList" :key="idx")
        component(:is="item.name" :isCurrent="currentSection === item.number" :next="next")

</template>

<script>
import Vue from 'vue';
import { mapGetters } from 'vuex';
import VueFullPage from 'vue-fullpage.js';

import QuestionList from './components/generic/QuestionList.vue';

import ChooseName from './components/ChooseName.vue';
import ChooseGender from './components/ChooseGender.vue';
import PreviewStage from './components/PreviewStage.vue';
import ChooseClass from './components/ChooseClass.vue';
import Dragon from './components/Dragon.vue';
import Dialog1 from './components/Dialog1.vue';
import Dialog2 from './components/Dialog2.vue';
import Dialog3 from './components/Dialog3.vue';
import Dialog4 from './components/Dialog4.vue';
import Landscape from './components/Landscape.vue';
import Landscape2 from './components/Landscape2.vue';
import Dialog5 from './components/Dialog5.vue';
import Princesses from './components/Princesses.vue';
import ChoosePrincesse from './components/ChoosePrincesse.vue';
import Dialog6 from './components/Dialog6.vue';
import Dialog7 from './components/Dialog7.vue';
import LetsGo from './components/LetsGo.vue';
import Dialog8 from './components/Dialog8.vue';
import OldBoy from './components/OldBoy.vue';
import Dialog9 from './components/Dialog9.vue';
import Dialog10 from './components/Dialog10.vue';
import AfterOldBoy from './components/AfterOldBoy.vue';
import Dialog11 from './components/Dialog11.vue';
import Dialog12 from './components/Dialog12.vue';
import Dialog13 from './components/Dialog13.vue';
import Dialog14 from './components/Dialog14.vue';
import Dialog15 from './components/Dialog15.vue';
import Dialog16 from './components/Dialog16.vue';
import Dialog17 from './components/Dialog17.vue';
import Dialog18 from './components/Dialog18.vue';
import Dialog19 from './components/Dialog19.vue';
import Dialog20 from './components/Dialog20.vue';
import Dialog21 from './components/Dialog21.vue';
import Dialog22 from './components/Dialog22.vue';
import Dialog23 from './components/Dialog23.vue';
import Dialog24 from './components/Dialog24.vue';
import RoadWithPrincesse from './components/RoadWithPrincesse.vue';


Vue.use(VueFullPage);
Vue.component('question-list', QuestionList);

const ALLOWED_SCROLLING_SECTIONS = [1, 5, 10, 12, 16, 18, 21, 25, 34];

const GAME_SECTIONS = {
  PreviewStage,
  ChooseGender,
  ChooseName,
  ChooseClass,
  Dragon,
  Dialog1,
  Dialog2,
  Dialog3,
  Dialog4,
  Landscape,
  Dialog5,
  Princesses,
  ChoosePrincesse,
  Dialog6,
  Dialog7,
  LetsGo,
  Dialog8,
  OldBoy,
  Dialog9,
  Dialog10,
  AfterOldBoy,
  Dialog11,
  Dialog12,
  Dialog13,
  Landscape2,
  Dialog14,
  Dialog15,
  Dialog16,
  Dialog17,
  Dialog18,
  Dialog19,
  Dialog20,
  Dialog21,
  RoadWithPrincesse,
  Dialog22,
  Dialog23,
  Dialog24,
};

export const getGameLength = () => (Object.keys(GAME_SECTIONS).length);

const gameLength = getGameLength();

export default {
  components: {
    ...GAME_SECTIONS,
  },
  data() {
    return {
      options: {
        onLeave: this.onLeave,
      },
      stagesList: Object.keys(GAME_SECTIONS).map((val, idx) => ({ name: val, number: idx + 1 })),
    };
  },
  computed: {
    ...mapGetters({
      currentSection: 'getCurrentSection',
    }),
    stagesPercent() {
      return (100 / gameLength) * this.currentSection;
    },
  },
  created() {
    this.stagesList = this.stagesList.slice(this.currentSection - 1);
  },
  mounted() {
    this.scrollingStatusUpdate();
  },
  methods: {
    next() {
      this.$refs.fullpage.api.moveSectionDown();
    },
    enableScrolling() {
      this.$refs.fullpage.api.setAllowScrolling(true, 'down');
      this.$refs.fullpage.api.setKeyboardScrolling(true, 'down');
    },
    disableScrolling() {
      this.$refs.fullpage.api.setAllowScrolling(false);
      this.$refs.fullpage.api.setKeyboardScrolling(false);
    },
    onLeave() {
      this.$store.dispatch('SET_CURRENT_SECTION', { currentSection: this.currentSection + 1 });
      this.scrollingStatusUpdate();
    },
    scrollingStatusUpdate() {
      if (ALLOWED_SCROLLING_SECTIONS.includes(this.currentSection)) {
        this.enableScrolling();
      } else {
        this.disableScrolling();
      }
    },
  },
};
</script>


